import TicTacToeGrid from "./TicTacToeGrid";
import CoinFlip from './CoinFlip';
import { createSignal } from "solid-js";
const [isPlayer,setPlayer] = createSignal(false);
const [xGoesFirst,setXGoesFirst] = createSignal(true);
const [key,setKey] = createSignal(true);


function App() {
  return (
    <div className="app">
      <div>
        <label>
          <input
            type="checkbox"
            checked={isPlayer()}
            onChange={(e) => setPlayer(e.target.checked)}
          />
          PVE
        </label>
      </div>
      <div>
        <label>
          <input
            type="checkbox"
            checked={xGoesFirst()}
            onChange={(e) => setXGoesFirst(e.target.checked)}
          />
          X Goes First
        </label>
      </div>
      <button onClick={()=>{
        setKey(false);
        setKey(true);
      }}>
        reset
      </button>
      <Show when={key()} keyed={false}>{
        ()=><TicTacToeGrid xNext={xGoesFirst()} player={isPlayer()}/>
      }</Show>
      
    </div>
  );
}

export default App;
