import { createSignal, createEffect, on } from "solid-js";
import styles from './App.module.css';

function TicTacToeGrid({xNext,player}) {
  const [board, setBoard] = createSignal(Array(9).fill(0));
  const [isXNext, setIsXNext] = createSignal(xNext);
  //setIsXNext(Math.random() < 0.5);
  const [isCompMove, setIsCompMove] = createSignal(false);
  const getMe = ()=>isXNext() ? 1 : -1;
  const win = (board) => {
    const wins = [
      [0, 1, 2], [3, 4, 5], [6, 7, 8],
      [0, 3, 6], [1, 4, 7], [2, 5, 8],
      [0, 4, 8], [2, 4, 6]
    ];

    for (const [a, b, c] of wins)
      if (board[a] !== 0 && board[a] === board[b] && board[b] === board[c])
        return board[a];
    return 0; // draw
  };

  const minimax = (board, player) => {
    const winner = win(board);
    if (winner !== 0) return winner * player;

    let move = -1;
    let score = -2;

    for (let i = 0; i < 9; i++) {
      if (board[i] === 0) {
        board[i] = player; // test score
        const thisScore = -minimax(board, player * -1);
        board[i] = 0;
        if (thisScore > score) {
          score = thisScore;
          move = i;
        }
        
      }
    }
    return move === -1 ? 0 : score;
  };

  const computerMoves = (board) => {
    let move = [];
    let score = -2;

    for (let i = 0; i < 9; ++i) {
      if (board[i] === 0) {
        board[i] = 1;
        const tempScore = -minimax(board, -1);
        board[i] = 0;
        if (tempScore > score) {
          score = tempScore;
          move = [i];
        } else if (tempScore === score) {
          move.push(i);
        }
      }
    }
    return move;
  };
  const autoPlay = (me) => {
    if (win(board()) !== 0) return;
    setIsCompMove(true);
    if (board().filter(e=>e===0).length === 0) return;
    setTimeout(()=>{
      const newBoard = board().slice();

      const compMoves = computerMoves( newBoard.map( e=> isXNext() ? e : -e ) );
      newBoard[compMoves[Math.floor(Math.random() * compMoves.length)]] = me;
      setBoard(newBoard);
      setIsXNext(!isXNext());
      setIsCompMove(false);
    },500);
  }

  const handleClick = (index) => {
    if (board()[index] !== 0 || win(board()) !== 0) return;

    const newBoard = board().slice();
    newBoard[index] = getMe(); // 1 for X, -1 for O
    setBoard(newBoard);
    setIsXNext(!isXNext());
  };

  const renderSquare = (index) => (
      <button className={styles.square} onClick={() => handleClick(index)} disabled={isCompMove()}>
        {board()[index] === 1 ? "X" : board()[index] === -1 ? "O" : null}
      </button>
    );

  const winner = ()=> win(board());
  const status = ()=> {
    if (winner() !== 0) return `Winner: ${winner() === 1 ? "X" : "O"}`;
    return board().filter(e=>e===0).length === 0 ? 'DRAW' : `Next player: ${isXNext() ? "X" : "O"}`;
  };

  if (!player || isXNext()) {

    autoPlay(getMe());
  }

  createEffect(on(isXNext, isXNext => {
    if (isXNext || !player) autoPlay(getMe());
  }, { defer: true }));

  return (
    <div>
      <div className={styles.status}>{status}</div>
      <div className={styles["board-row"]}>
        {renderSquare(0)}
        {renderSquare(1)}
        {renderSquare(2)}
      </div>
      <div className={styles["board-row"]}>
        {renderSquare(3)}
        {renderSquare(4)}
        {renderSquare(5)}
      </div>
      <div className={styles["board-row"]}>
        {renderSquare(6)}
        {renderSquare(7)}
        {renderSquare(8)}
      </div>
    </div>
  );
}

export default TicTacToeGrid;